import React from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import Button from '@material-ui/core/Button';
// import BookMarcLogo from '../logo.jpg';
import AlertDialogSlide from './LoginDialog';
import { FacebookIcon } from 'react-share';
import { FacebookProvider, Like } from 'react-facebook';

const useStyles = makeStyles((theme) => ({
	logo: {
		maxWidth: 160,
		display: 'flex'
	},
	grow: {
		flexGrow: 1
	},
	menuButton: {
		marginRight: theme.spacing(2)
	},
	title: {
		display: 'none',
		[theme.breakpoints.up('sm')]: {
			display: 'block'
		}
	},
	search: {
		position: 'relative',
		borderRadius: theme.shape.borderRadius,
		backgroundColor: fade(theme.palette.common.white, 0.15),
		'&:hover': {
			backgroundColor: fade(theme.palette.common.white, 0.45)
		},
		marginRight: theme.spacing(2),
		marginLeft: 0,
		width: '100%',
		[theme.breakpoints.up('sm')]: {
			marginLeft: theme.spacing(3),
			width: 'auto'
		}
	},
	searchIcon: {
		width: theme.spacing(7),
		height: '100%',
		position: 'absolute',
		pointerEvents: 'none',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center'
	},
	inputRoot: {
		color: 'inherit'
	},
	inputInput: {
		padding: theme.spacing(1, 1, 1, 7),
		transition: theme.transitions.create('width'),
		width: '100%',
		[theme.breakpoints.down('lg')]: {
			width: 750
		},
		[theme.breakpoints.down('md')]: {
			width: 400
		},

		[theme.breakpoints.down('xs')]: {
			width: 160
		}
	},
	sectionDesktop: {
		display: 'none',
		[theme.breakpoints.up('md')]: {
			display: 'flex'
		}
	},
	sectionMobile: {
		display: 'flex',
		[theme.breakpoints.up('md')]: {
			display: 'none'
		}
	}
}));

export default function PrimarySearchAppBar(props) {
	const classes = useStyles();

	const [anchorEl, setAnchorEl] = React.useState(null);
	const [openDialog, setOpenDialog] = React.useState(false);
	const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

	const isMenuOpen = Boolean(anchorEl);
	const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

	const handleProfileMenuOpen = (event) => {
		setAnchorEl(event.currentTarget);
		console.log('[TopBar.js] handleProfileMenuOpen,', event);
	};

	const handleMobileMenuClose = () => {
		setMobileMoreAnchorEl(null);
		console.log('[TopBar.js] handleMobileMenuClose,');
	};

	const handleMenuClose = () => {
		setAnchorEl(null);
		handleMobileMenuClose();
		console.log('[TopBar.js] handleMenuClose,');
	};

	const handleMobileMenuOpen = (event) => {
		setMobileMoreAnchorEl(event.currentTarget);
		console.log('[TopBar.js] handleMobileMenuOpen,', event);
	};

	const menuId = 'primary-search-account-menu';
	const renderMenu = (
		<Menu
			disableScrollLock={true}
			anchorEl={anchorEl}
			anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
			id={menuId}
			keepMounted
			transformOrigin={{ vertical: 'top', horizontal: 'right' }}
			open={isMenuOpen}
			onClose={handleMenuClose}
			style={{ marginTop: '30px' }}
		>
			<MenuItem
				onClick={() => {
					handleMenuClose();
					props.history.push('/account');
				}}
			>
				Profile
			</MenuItem>
			<MenuItem
				onClick={() => {
					handleMenuClose();
					props.history.push('/account');
				}}
			>
				My account
			</MenuItem>
		</Menu>
	);

	const mobileMenuId = 'primary-search-account-menu-mobile';
	const renderMobileMenu = (
		<Menu
			disableScrollLock={true}
			anchorEl={mobileMoreAnchorEl}
			anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
			id={mobileMenuId}
			keepMounted
			transformOrigin={{ vertical: 'top', horizontal: 'right' }}
			open={isMobileMenuOpen}
			onClose={handleMobileMenuClose}
		>
			<MenuItem>
				<IconButton aria-label='show 4 new items' color='inherit'>
					<Badge badgeContent={4} color='secondary'>
						<ShoppingCartIcon></ShoppingCartIcon>
					</Badge>
				</IconButton>
				<p>Cart</p>
			</MenuItem>
			<MenuItem>
				<IconButton aria-label='show 11 new notifications' color='inherit'>
					<Badge badgeContent={11} color='secondary'>
						<NotificationsIcon />
					</Badge>
				</IconButton>
				<p>Notifications</p>
			</MenuItem>
			<MenuItem onClick={handleProfileMenuOpen}>
				<IconButton
					aria-label='account of current user'
					aria-controls='primary-search-account-menu'
					aria-haspopup='true'
					color='inherit'
				>
					<AccountCircle />
				</IconButton>
				<p
					onClick={() => {
						props.history.push('/account');
					}}
				>
					Profile
				</p>
			</MenuItem>
		</Menu>
	);

	return (
		<div className={classes.grow}>
			<AppBar position='static'>
				<Toolbar>
					<IconButton
						edge='start'
						className={classes.menuButton}
						color='inherit'
						aria-label='open drawer'
					>
						<MenuIcon />
					</IconButton>
					{/* <img
						src={BookMarcLogo}
						alt='bookmarc-logo'
						className={classes.logo}
					></img> */}
					<div>
						<Typography
							className={classes.title}
							variant='h6'
							noWrap
							style={{ cursor: 'pointer' }}
							onClick={() => props.history.push('/')}
						>
							BookMarcs
						</Typography>
						<Typography
							className={classes.title}
							variant='span'
							noWrap
							style={{
								cursor: 'pointer',
								marginTop: '-5px',
								'font-style': 'italic',

								color: 'lightgreen',
								border: 'solid'
							}}
							onClick={() => props.history.push('/')}
						>
							Frequent Buyer
						</Typography>
					</div>

					<div className={classes.search}>
						<div className={classes.searchIcon}>
							<SearchIcon />
						</div>
						<InputBase
							placeholder='Search'
							classes={{
								root: classes.inputRoot,
								input: classes.inputInput
							}}
							inputProps={{
								'aria-label': 'search',
								onClick: props.openSearchBar
							}}
						/>
					</div>
					<div className={classes.grow} />
					<div className={classes.sectionDesktop}>
						<Button
							variant='contained'
							color='Secondary'
							style={{ margin: '10px' }}
							// onClick={() => setOpenDialog(true)}
							setOpenDialog={setOpenDialog}
						>
							<span style={{ paddingLeft: '1.2vw' }}>Follow Us on </span>
							<FacebookIcon style={{ height: '3vh' }}></FacebookIcon>
						</Button>
						<IconButton
							aria-label='show 4 new items'
							color='inherit'
							onClick={() => props.history.push('/cart')}
						>
							<Badge badgeContent={4} color='secondary'>
								<ShoppingCartIcon></ShoppingCartIcon>
							</Badge>
						</IconButton>
						<IconButton aria-label='show 17 new notifications' color='inherit'>
							<Badge badgeContent={17} color='secondary'>
								<NotificationsIcon />
							</Badge>
						</IconButton>
						{props.signedIn ? (
							<IconButton
								edge='end'
								aria-label='account of current user'
								aria-controls={menuId}
								aria-haspopup='true'
								onClick={handleProfileMenuOpen}
								color='inherit'
							>
								<AccountCircle />
							</IconButton>
						) : (
							<Button
								variant='contained'
								color='Secondary'
								style={{ margin: '10px' }}
								onClick={() => setOpenDialog(true)}
								setOpenDialog={setOpenDialog}
							>
								Login
							</Button>
						)}
					</div>
					<div className={classes.sectionMobile}>
						<IconButton
							aria-label='show more'
							aria-controls={mobileMenuId}
							aria-haspopup='true'
							onClick={handleMobileMenuOpen}
							color='inherit'
						>
							<MoreIcon />
						</IconButton>
					</div>
				</Toolbar>
			</AppBar>
			{renderMobileMenu}
			{renderMenu}
			<AlertDialogSlide
				open={openDialog}
				onClose={() => setOpenDialog(false)}
				login={props.login}
			></AlertDialogSlide>
		</div>
	);
}
