import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { Paper } from '@material-ui/core';
import { BrowserRouter as Router, Switch, useLocation } from 'react-router-dom';

function TabPanel(props) {
	const { children, value, index, ...other } = props;

	return (
		<Typography
			component='div'
			role='tabpanel'
			hidden={value !== index}
			id={`scrollable-auto-tabpanel-${index}`}
			aria-labelledby={`scrollable-auto-tab-${index}`}
			{...other}
		>
			{value === index && <Box p={3}>{children}</Box>}
		</Typography>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.any.isRequired,
	value: PropTypes.any.isRequired
};

function a11yProps(index) {
	return {
		id: `scrollable-auto-tab-${index}`,
		'aria-controls': `scrollable-auto-tabpanel-${index}`
	};
}

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
		width: '100%',
		backgroundColor: theme.palette.background.paper
	}
}));

export default function ScrollableTabsButtonAuto(props) {
	const classes = useStyles();
	const location = useLocation();
	React.useEffect(() => {
		if (location.pathname === '/') {
			setValue(null);
		}
		console.log(location);
	}, [location]);

	const [value, setValue] = React.useState(null);

	const handleChange = (event, newValue) => {
		setValue(newValue);
		props.history.push(`/category/${tabs[newValue]}`);
	};
	const tabs = [
		'Art',
		'History',
		'Literature',
		'Fiction',
		'Military',
		'Cookbooks & Entertaining',
		'Religious'
	];

	return (
		<div className={classes.root}>
			<Paper color='default'>
				<Tabs
					value={value}
					onChange={handleChange}
					indicatorColor='primary'
					textColor='primary'
					scrollButtons='on'
					variant='fullWidth'
					// variant='scrollable'
					centered
					aria-label='scrollable auto tabs example'
				>
					{tabs.map((tab, index) => (
						<Tab label={tab} {...a11yProps(index)}></Tab>
					))}
					{/* <Tab label='Art' {...a11yProps(0)} />
					<Tab label='History' {...a11yProps(1)} />
					<Tab label='Literature' {...a11yProps(2)} />
					<Tab label='Fiction' {...a11yProps(3)} />
					<Tab label='Military' {...a11yProps(4)} />
					<Tab label='Cookbooks & Entertaining' {...a11yProps(5)} />
					<Tab label='Religious' {...a11yProps(6)} /> */}
				</Tabs>
			</Paper>
			{/* <TabPanel value={value} index={0}>
				Art
			</TabPanel>
			<TabPanel value={value} index={1}>
				History
			</TabPanel>
			<TabPanel value={value} index={2}>
				Literature
			</TabPanel>
			<TabPanel value={value} index={3}>
				Fiction
			</TabPanel>
			<TabPanel value={value} index={4}>
				Religious
			</TabPanel>
			<TabPanel value={value} index={5}>
				Cookbooks & Entertaining
			</TabPanel>
			<TabPanel value={value} index={6}>
				Military
			</TabPanel> */}
		</div>
	);
}
