import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Footer from 'rc-footer';
import 'rc-footer/assets/index.css';

import YouTubeIcon from '@material-ui/icons/YouTube';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import { Link } from '@material-ui/core';
const useStyles = makeStyles((theme) => ({
	root: {
		display: 'flex',
		flexWrap: 'wrap',
		'& > *': {
			margin: theme.spacing(1),
			width: theme.spacing(16),
			height: theme.spacing(16)
		}
	}
}));

export default function SiteFooter(props) {
	const classes = useStyles();

	return (
		<Footer
			theme='dark'
			backgroundColor='#172337'
			columns={[
				{
					title: 'About',
					url: '#',
					openExternal: true,
					items: [
						{ title: 'Contact Us' },
						{ title: 'About Us' },
						{
							title: 'Bookmarc Blogs',
							LinkComponent: () => {
								return (
									<Link
										to='/blogs'
										onClick={() => {
											props.history.push('/blogs');
										}}
									>
										Bookmarcs Blogs
									</Link>
								);
							}
						}
					]
				},
				{
					title: 'Help',
					url: '#',
					openExternal: true,
					items: [
						{ title: 'Payments' },
						{ title: 'Shipping' },
						{ title: 'FAQ' }
					]
				},
				{
					title: 'Social',
					url: '#',
					openExternal: true,
					items: [
						{ title: 'Facebook', icon: <FacebookIcon></FacebookIcon> },
						{ title: 'Twitter', icon: <TwitterIcon></TwitterIcon> },
						{ title: 'Youtube', icon: <YouTubeIcon></YouTubeIcon> }
					]
				},
				{
					title: 'Registered Office Address',
					openExternal: true,
					columnLayout: '',
					items: [
						{
							title:
								'Address : 3302 Clarksville St. La Porte, TX 77571-3864, United States',
							LinkComponent: 'div',
							style: { color: 'rgba(255, 255, 255, 1.0)' }
						},
						{
							title: 'Email : bookmarcs@gmail.com',
							LinkComponent: 'object',
							style: { color: 'rgba(255, 255, 255, 1.0)' }
						},
						{
							title: 'Phone : (281) 479-0596',
							LinkComponent: 'object',
							style: { color: 'rgba(255, 255, 255, 1.0)' }
						}
					]
				}
			]}
			bottom=' &copy; Bookmarcs 2019'
		></Footer>
		// <div className={classes.root}>
		//  <Paper elevation={3}>

		// </Paper>
		// </div>
	);
}
