import React, { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CancelIcon from '@material-ui/icons/Cancel';
import { GoogleLogin } from 'react-google-login';
import LoginSignUpGoogleButton from './LoginSignUpGoogleButton.css';

function Copyright() {
	return (
		<Typography variant='body2' color='textSecondary' align='center'>
			{'Copyright  '}
			<Link color='inherit' href='https://material-ui.com/'>
				Bookmarcs
			</Link>{' '}
			{new Date().getFullYear()}
			{'.'}
		</Typography>
	);
}

const useStyles = makeStyles((theme) => ({
	paper: {
		marginTop: theme.spacing(1),
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center'
	},
	avatar: {
		margin: theme.spacing(1),
		backgroundColor: theme.palette.secondary.main
	},
	form: {
		width: '100%', // Fix IE 11 issue.
		marginTop: theme.spacing(0)
	},
	submit: {
		margin: theme.spacing(3, 0, 2)
	}
}));

export default function SignIn(props) {
	const classes = useStyles();
	const [showSignUp, setShowSignUp] = useState(false);
	console.log(props);
	const singIn = (
		<Container component='main' maxWidth='xs'>
			<div
				style={{
					position: 'absolute',
					right: '0px',
					top: '0px',
					cursor: 'pointer'
				}}
			>
				<CancelIcon onClick={props.close}></CancelIcon>
			</div>
			<CssBaseline />
			<div className={classes.paper}>
				<Avatar className={classes.avatar}>
					<LockOutlinedIcon />
				</Avatar>
				<Typography component='h1' variant='h5'>
					Sign in
				</Typography>
				<div className={classes.form} noValidate>
					<TextField
						variant='outlined'
						margin='normal'
						required
						fullWidth
						id='email'
						label='Email Address'
						name='email'
						autoComplete='email'
						autoFocus
					/>
					<TextField
						variant='outlined'
						margin='normal'
						required
						fullWidth
						name='password'
						label='Password'
						type='password'
						id='password'
						autoComplete='current-password'
					/>
					<FormControlLabel
						control={<Checkbox value='remember' color='primary' />}
						label='Remember me'
					/>
					<Button
						type='submit'
						fullWidth
						variant='contained'
						color='primary'
						className={classes.submit}
						onClick={() => {
							props.login();
							props.close();
						}}
					>
						Sign In
					</Button>
					<Grid container>
						<Grid item xs>
							<Link variant='body2'>Forgot password?</Link>
						</Grid>
						<Grid item>
							<Link
								href='#'
								variant='body2'
								onClick={(e) => {
									e.preventDefault();

									console.log('[LoginSignUp.js] Inside SetShowSingUp');

									setShowSignUp(true);
								}}
							>
								{"Don't have an account? Sign Up"}
							</Link>
						</Grid>
					</Grid>
					<div>
						<div style={{ textAlign: 'center', padding: '10px' }}>
							<span style={{ textAlign: 'center' }}>OR</span>
						</div>
						<div>
							<GoogleLogin
								style={{
									width: '-webkit-fill-available',
									justifyContent: 'center',
									fontSize: '100px',
									background: 'black'
								}}
								render={(renderProps) => (
									<div>
										<button
											type='button'
											className='google-button-class'
											onClick={renderProps.onClick}
										>
											<div className='google-button-div-class'>
												<svg
													width='18'
													height='18'
													xmlns='http://www.w3.org/2000/svg'
												>
													<g fill='#000' fill-rule='evenodd'>
														<path
															d='M9 3.48c1.69 0 2.83.73 3.48 1.34l2.54-2.48C13.46.89 11.43 0 9 0 5.48 0 2.44 2.02.96 4.96l2.91 2.26C4.6 5.05 6.62 3.48 9 3.48z'
															fill='#EA4335'
														></path>
														<path
															d='M17.64 9.2c0-.74-.06-1.28-.19-1.84H9v3.34h4.96c-.1.83-.64 2.08-1.84 2.92l2.84 2.2c1.7-1.57 2.68-3.88 2.68-6.62z'
															fill='#4285F4'
														></path>
														<path
															d='M3.88 10.78A5.54 5.54 0 0 1 3.58 9c0-.62.11-1.22.29-1.78L.96 4.96A9.008 9.008 0 0 0 0 9c0 1.45.35 2.82.96 4.04l2.92-2.26z'
															fill='#FBBC05'
														></path>
														<path
															d='M9 18c2.43 0 4.47-.8 5.96-2.18l-2.84-2.2c-.76.53-1.78.9-3.12.9-2.38 0-4.4-1.57-5.12-3.74L.97 13.04C2.45 15.98 5.48 18 9 18z'
															fill='#34A853'
														></path>
														<path fill='none' d='M0 0h18v18H0z'></path>
													</g>
												</svg>
											</div>
											<span
												style={{
													padding: '10px 10px 10px 0px',
													'font-weight': 500
												}}
											>
												Login With Google
											</span>
										</button>
									</div>
								)}
								clientId='556191573231-vavpa1s80dqus267agv6b9ukddmudlf5.apps.googleusercontent.com'
								buttonText='Login With Google'
								onSuccess={(res) => {
									console.log(res);
									localStorage.setItem('userName', res.profileObj.name);
									localStorage.setItem('imageUrl', res.profileObj.imageUrl);
									props.login();
									props.close();
								}}
								onFailure={(res) => {
									console.log(res);
									props.login();
									props.close();
								}}
								cookiePolicy={'single_host_origin'}
							/>
						</div>
					</div>
				</div>
			</div>

			<Box mt={1}>
				<Copyright />
			</Box>
		</Container>
	);
	const signUp = (
		<Container component='main' maxWidth='xs'>
			<div
				style={{
					position: 'absolute',
					right: '0px',
					top: '0px',
					cursor: 'pointer'
				}}
			>
				<CancelIcon onClick={props.close}></CancelIcon>
			</div>
			<CssBaseline />
			<div className={classes.paper}>
				<Avatar className={classes.avatar}>
					<LockOutlinedIcon />
				</Avatar>
				<Typography component='h1' variant='h5'>
					Sign Up
				</Typography>
				<div className={classes.form} noValidate>
					<TextField
						variant='outlined'
						margin='normal'
						required
						fullWidth
						id='email'
						label='Email Address'
						name='email'
						autoComplete='email'
						autoFocus
					/>
					<TextField
						variant='outlined'
						margin='normal'
						required
						fullWidth
						name='password'
						label='Password'
						type='password'
						id='password'
						autoComplete='current-password'
					/>
					<FormControlLabel
						control={
							<Checkbox value='remember' color='primary' checked={true} />
						}
						label='Get Newsletter'
					/>
					<FormControlLabel
						control={<Checkbox value='remember' color='primary' />}
						label='Remember me'
					/>
					<Button
						type='submit'
						fullWidth
						variant='contained'
						color='primary'
						className={classes.submit}
						onClick={() => {
							props.login();
							props.close();
						}}
					>
						Sign Up
					</Button>

					<div>
						<div style={{ textAlign: 'center', padding: '10px' }}>
							<span style={{ textAlign: 'center' }}>OR</span>
						</div>
						<div>
							<GoogleLogin
								style={{
									width: '-webkit-fill-available',
									justifyContent: 'center',
									fontSize: '100px',
									background: 'black'
								}}
								render={(renderProps) => (
									<div>
										<button
											type='button'
											className='google-button-class'
											onClick={renderProps.onClick}
										>
											<div className='google-button-div-class'>
												<svg
													width='18'
													height='18'
													xmlns='http://www.w3.org/2000/svg'
												>
													<g fill='#000' fill-rule='evenodd'>
														<path
															d='M9 3.48c1.69 0 2.83.73 3.48 1.34l2.54-2.48C13.46.89 11.43 0 9 0 5.48 0 2.44 2.02.96 4.96l2.91 2.26C4.6 5.05 6.62 3.48 9 3.48z'
															fill='#EA4335'
														></path>
														<path
															d='M17.64 9.2c0-.74-.06-1.28-.19-1.84H9v3.34h4.96c-.1.83-.64 2.08-1.84 2.92l2.84 2.2c1.7-1.57 2.68-3.88 2.68-6.62z'
															fill='#4285F4'
														></path>
														<path
															d='M3.88 10.78A5.54 5.54 0 0 1 3.58 9c0-.62.11-1.22.29-1.78L.96 4.96A9.008 9.008 0 0 0 0 9c0 1.45.35 2.82.96 4.04l2.92-2.26z'
															fill='#FBBC05'
														></path>
														<path
															d='M9 18c2.43 0 4.47-.8 5.96-2.18l-2.84-2.2c-.76.53-1.78.9-3.12.9-2.38 0-4.4-1.57-5.12-3.74L.97 13.04C2.45 15.98 5.48 18 9 18z'
															fill='#34A853'
														></path>
														<path fill='none' d='M0 0h18v18H0z'></path>
													</g>
												</svg>
											</div>
											<span
												style={{
													padding: '10px 10px 10px 0px',
													'font-weight': 500
												}}
											>
												Login With Google
											</span>
										</button>
									</div>
								)}
								clientId='556191573231-vavpa1s80dqus267agv6b9ukddmudlf5.apps.googleusercontent.com'
								buttonText='Login With Google'
								onSuccess={(res) => {
									console.log(res);
									props.login();
									props.close();
								}}
								onFailure={(res) => {
									console.log(res);
									props.login();
									props.close();
								}}
								cookiePolicy={'single_host_origin'}
							/>
						</div>
					</div>
				</div>
			</div>

			<Box mt={1}>
				<Copyright />
			</Box>
		</Container>
	);

	return <React.Fragment>{showSignUp ? signUp : singIn}</React.Fragment>;
}
