import React, { Component } from 'react';
import Marquee from 'react-text-marquee';
import './App.css';

export default class MarqueeText extends Component {
	render() {
		return (
			<div style={{ marginTop: '10px', marginBottom: '10px' }}>
				<Marquee
					hoverToStop={true}
					loop={true}
					text='This is Post 1.	This is Post 2	This is Post 3.   This is Post 4.	This is Post 5.   This is Post 6.	This is Post 7.   This is Post 8.	This is Post 9.   This is Post 10.	This is Post 11.   This is Post 12.	This is Post 13.   This is Post 14.	This is Post 15.   This is Post 12.	This is Post 16.   This is Post 17.'
				/>
			</div>
		);
	}
}
