import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { Typography, Divider } from '@material-ui/core';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import ProfilePic from '../profile-pic.svg';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ordersImage from '../ordersImage';
import AccountImage from '../AccountImage';
import Address from './Address';
// import Orders from './Orders';
const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
		background: 'white'
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',

		background: '#eee'
	}
}));

export default function Profile(props) {
	const classes = useStyles();
	console.log(ordersImage);

	return (
		<div className={classes.root}>
			<Grid
				container
				justify='space-around'
				alignItems='flex-start'
				style={{ marginBottom: '10px' }}
			>
				<Grid item xs={3}>
					{/* <Grid
						container
						direction='column'
						justify='flex-start'
						alignItems='center'
					>
						<Grid item xs={12}>
							<Paper className={classes.paper} style={{ textAlign: 'left' }}>
								<Grid container alignItems='center'>
									<Grid item>
										<img src={ProfilePic}></img>
									</Grid>
									<Grid item style={{ paddingLeft: '10px' }}>
										<Typography style={{ fontSize: '13px' }}>Hello,</Typography>
										<Typography style={{ fontWeight: 'bold' }}>
											{' '}
											John Doe
										</Typography>
									</Grid>
								</Grid>
							</Paper>
						</Grid>
						<Grid item xs={12}>
							<Paper className={classes.paper}>jdhfksjdh</Paper>
						</Grid>
					</Grid> */}
					<Paper
						className={classes.paper}
						style={{ textAlign: 'left', cursor: 'pointer' }}
					>
						<Grid container alignItems='center'>
							<Grid item>
								<img src={localStorage.getItem('imageUrl') || ProfilePic}></img>
							</Grid>
							<Grid item style={{ paddingLeft: '10px' }}>
								<Typography style={{ fontSize: '13px' }}>Hello,</Typography>
								<Typography style={{ fontWeight: 'bold' }}>
									{' '}
									{localStorage.getItem('userName') || 'John Doe'}
								</Typography>
							</Grid>
						</Grid>
					</Paper>

					<Paper
						className={classes.paper}
						style={{ textAlign: 'left', marginTop: '15px' }}
					>
						<Grid
							container
							spacing={3}
							justify='space-around'
							style={{ paddingTop: '20px', cursor: 'pointer' }}
							alignItems='flex-start'
							onClick={() => {
								props.history.push('/account/orders');
							}}
						>
							<Grid>
								<img src={ordersImage}></img>
								<Typography component='span' style={{ paddingLeft: '25px' }}>
									My Orders
								</Typography>
							</Grid>
							<Grid></Grid>
							<Grid>
								<ArrowForwardIosIcon></ArrowForwardIosIcon>
							</Grid>
						</Grid>
						<Divider style={{ marginTop: '40px' }}></Divider>
						<Grid
							container
							spacing={3}
							justify='space-around'
							style={{ marginTop: '22px' }}
						>
							<Grid>
								<img src={AccountImage}></img>
								<Typography component='span' style={{ paddingLeft: '25px' }}>
									Account Settings
								</Typography>
							</Grid>
							<Grid></Grid>
							<Grid></Grid>
						</Grid>
						<Grid
							container
							spacing={3}
							justify='space-around'
							style={{ marginTop: '22px', cursor: 'pointer' }}
						>
							<Grid>
								<Typography
									component='span'
									style={{
										paddingLeft: '50px',

										cursor: 'pointer'
									}}
								>
									Profile Information
								</Typography>
							</Grid>
							<Grid></Grid>
							<Grid></Grid>
						</Grid>
						<Grid
							container
							spacing={3}
							justify='space-around'
							style={{ marginTop: '22px', cursor: 'pointer' }}
						>
							<Grid>
								<Typography
									component='span'
									style={{ paddingLeft: '50px', color: '#2874f0' }}
								>
									Manage Addresses
								</Typography>
							</Grid>
							<Grid></Grid>
							<Grid></Grid>
						</Grid>

						<Divider style={{ marginTop: '40px' }}></Divider>
						<Grid
							container
							spacing={3}
							justify='space-around'
							alignItems='flex-start'
							style={{ marginTop: '22px', paddingBottom: '20px' }}
						>
							<Grid>
								<svg
									width='24'
									height='24'
									class=''
									viewBox='0 0 24 24'
									xmlns='http://www.w3.org/2000/svg'
								>
									<path
										fill='#2874F0'
										stroke-width='0.3'
										stroke='#2874F0'
										d='M13 3h-2v10h2V3zm4.83 2.17l-1.42 1.42C17.99 7.86 19 9.81 19 12c0 3.87-3.13 7-7 7s-7-3.13-7-7c0-2.19 1.01-4.14 2.58-5.42L6.17 5.17C4.23 6.82 3 9.26 3 12c0 4.97 4.03 9 9 9s9-4.03 9-9c0-2.74-1.23-5.18-3.17-6.83z'
									></path>
								</svg>
								<Typography
									component='span'
									style={{ paddingLeft: '25px', cursor: 'pointer' }}
								>
									Logout
									&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
								</Typography>
							</Grid>
							<Grid></Grid>
							<Grid></Grid>
						</Grid>
						{/* <Grid
							container
							direction='column'
							justify='flex-end'
							alignItems='center'
							// style
						>
							<Grid>Profile Information</Grid>
							<Grid>Manage Addresses</Grid>
							<Grid>Profile Information</Grid>
						</Grid> */}
						{/* <Grid
							container
							spacing={3}
							justify='space-around'
							style={{ paddingTop: '20px', marginTop: '2px' }}
						>
							<Grid>
							
								<Typography component='span'>Logout</Typography>
							</Grid>
							<Grid></Grid>
							<Grid></Grid>
						</Grid> */}
					</Paper>
				</Grid>
				<Grid item xs={7}>
					<Paper className={classes.paper}>
						<Address></Address>
					</Paper>
				</Grid>
			</Grid>
		</div>
	);
}
