import React from 'react';
import range from 'lodash/range';
import styled from 'styled-components';
import ItemsCarousel from 'react-items-carousel';
import Button from '../components/button';
import NavigationIcon from '@material-ui/icons/Navigation';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import Card from '../components/CardComponent';
import PersonIcon from '@material-ui/icons/Person';

import UserImage from './user.png';
import { height } from 'dom-helpers';
const noOfItems = 9;
const noOfCards = 2;
const autoPlayDelay = 5000;
const chevronWidth = 40;

const Wrapper = styled.div`
	max-width: 100%;
	margin: 0 auto 10;
`;

const SlideItem = styled.div`
	height: 100px;
	background: #eee;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 20px;
	font-weight: bold;
`;

// const carouselItems = range(noOfItems).map((index) => (
// 	<SlideItem key={index}>{index + 1}</SlideItem>
// ));

export default class AutoPlayCarousel extends React.Component {
	state = {
		activeItemIndex: 0
	};

	componentDidMount() {
		this.interval = setInterval(this.tick, autoPlayDelay);
	}

	componentWillUnmount() {
		clearInterval(this.interval);
	}

	tick = () =>
		this.setState((prevState) => ({
			activeItemIndex:
				(prevState.activeItemIndex + 1) % (noOfItems - noOfCards + 1)
		}));

	onChange = (value) => this.setState({ activeItemIndex: value });

	render() {
		const carouselItems = Array(9)
			.fill(1)
			.map((item, index) => (
				<SlideItem
					key={index}
					style={{ textAlign: 'center', cursor: 'pointer' }}
				>
					<div
						style={{ textAlign: 'center' }}
						onClick={() => window.open('https://www.facebook.com')}
					>
						<img src={UserImage} style={{ height: '50px' }}></img>
						<div>Post {index}- Facebook post about Bookmarcs</div>
					</div>
				</SlideItem>
			));
		return (
			<Wrapper style={{ marginBottom: '10px' }}>
				<ItemsCarousel
					gutter={12}
					numberOfCards={noOfCards}
					activeItemIndex={this.state.activeItemIndex}
					requestToChangeActive={this.onChange}
					// rightChevron={
					// 	<Button
					// 		style={{ paddingRight: '60px' }}
					// 		iconName={() => <ArrowForwardIosIcon></ArrowForwardIosIcon>}
					// 	></Button>
					// }
					// leftChevron={
					// 	<Button
					// 		style={{ paddingLeft: '60px' }}
					// 		iconName={() => <ArrowBackIosIcon></ArrowBackIosIcon>}
					// 	></Button>
					// }
					chevronWidth={chevronWidth}
					outsideChevron
					children={carouselItems}
				/>
			</Wrapper>
		);
	}
}
