import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Book1 from '../book-1.jpg';
import Book5 from '../book-5.jpg';
import Typography from '@material-ui/core/Typography';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import Remove from '@material-ui/icons/Remove';
import TextField from '@material-ui/core/TextField';
import { Button, Input, Checkbox } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import './Cart2.css';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Rating from '@material-ui/lab/Rating';
import InputBase from '@material-ui/core/InputBase';
const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary
	},
	inputNumber: {
		padding: theme.spacing(0)
	},
	formControl: {
		margin: theme.spacing(1),
		minWidth: 120,
		paddingTop: 10,
		paddingBottom: 10
	},
	withoutPadding: {
		padding: '0px'
	},
	input: {
		padding: 0
	},
	selectEmpty: {
		marginTop: theme.spacing(2)
	}
}));

export default function FullWidthGrid(props) {
	const classes = useStyles();
	const [quantity, setQuantity] = React.useState(1);
	const handleChange = (event) => {
		setQuantity(event.target.value);
	};
	const inputLabel = React.useRef(null);
	const [labelWidth, setLabelWidth] = React.useState(0);

	React.useEffect(() => {
		setLabelWidth(inputLabel.current.offsetWidth);
	}, []);

	return (
		<div className={classes.root}>
			<Grid
				container
				spacing={3}
				style={{ marginLeft: '10px', marginBottom: '10px' }}
			>
				<Grid item xs={8} spacing={1}>
					<Grid item xs={12}>
						<Grid
							container
							direction='row'
							justify='space-between'
							alignItems='flex-end'
						>
							<Grid item>
								<Typography>
									<h2 style={{ marginBottom: '0px' }}>Shopping Cart</h2>
								</Typography>
							</Grid>
							<Grid item>Price</Grid>
						</Grid>
					</Grid>
					<Divider></Divider>
					<Grid container style={{ marginTop: '20px' }}>
						<Grid item xs={2} style={{ textAlign: 'center' }}>
							<img src={Book1} width={'110px'}></img>
						</Grid>
						<Grid item xs={8}>
							<Grid container alignItems='center'>
								<Grid item xs={12}>
									<Typography
										style={{
											color: '#0066c0',
											fontWeight: 'bolder',
											fontSize: 'large',
											cursor: 'pointer'
										}}
										className='product-name'
									>
										Look Both Ways
									</Typography>
									<Typography>
										<Rating name='read-only' value={4} readOnly />
									</Typography>
									<Typography variant={'caption'}>
										Author: Jason Reynolds
									</Typography>
								</Grid>
								<Grid item xs={3} style={{ paddingTop: '5px' }}>
									<FormControl
										variant='outlined'
										className={classes.formControl}
									>
										<InputLabel
											ref={inputLabel}
											id='demo-simple-select-outlined-label'
											htmlFor='quantity'
										>
											Quantity
										</InputLabel>
										<Select
											// classes={{ input: classes.withoutPadding }}
											displayEmpty
											className={classes.withoutPadding}
											labelId='demo-simple-select-outlined-label'
											id='demo-simple-select-outlined'
											value={quantity}
											onChange={handleChange}
											labelWidth={labelWidth}
											defaultValue={1}
										>
											<MenuItem value={1} selected>
												1
											</MenuItem>
											<MenuItem value={2}>2</MenuItem>
											<MenuItem value={3}>3</MenuItem>
											<MenuItem value={4}>4</MenuItem>
											<MenuItem value={5}>5</MenuItem>
											<MenuItem value={6}>6</MenuItem>
											<MenuItem value={7}>7</MenuItem>
											<MenuItem value={8}>8</MenuItem>
											<MenuItem value={9}>9</MenuItem>
											<MenuItem value={10}>10+</MenuItem>
										</Select>
									</FormControl>
								</Grid>
								<Grid item xs={3}>
									<Button style={{ color: '#0066C0', fontSize: 'small' }}>
										{' '}
										Delete
									</Button>
								</Grid>
							</Grid>
						</Grid>
						<Grid item xs={2} style={{ textAlign: 'right' }}>
							<Typography style={{}}>
								<span
									style={{
										fontSize: 'large',
										color: '#B12704 ',
										fontWeight: 'bolder'
									}}
								>
									$87
								</span>
							</Typography>
						</Grid>
					</Grid>
					<Divider></Divider>
					<Grid container style={{ marginTop: '20px' }}>
						<Grid item xs={2} style={{ textAlign: 'center' }}>
							<img src={Book5} width={'110px'}></img>
						</Grid>
						<Grid item xs={8}>
							<Grid container alignItems='center'>
								<Grid item xs={12}>
									<Typography
										style={{
											color: '#0066c0',
											fontWeight: 'bolder',
											fontSize: 'large',
											cursor: 'pointer'
										}}
										className='product-name'
									>
										The Bishop
									</Typography>
									<Typography>
										<Rating name='read-only' value={5} readOnly />
									</Typography>
									<Typography variant={'caption'}>
										Author: Alan Lake Chidsey
									</Typography>
								</Grid>
								<Grid item xs={3} style={{ paddingTop: '5px' }}>
									<FormControl
										variant='outlined'
										className={classes.formControl}
									>
										<InputLabel
											ref={inputLabel}
											id='demo-simple-select-outlined-label'
											htmlFor='quantity'
										>
											Quantity
										</InputLabel>
										<Select
											// classes={{ input: classes.withoutPadding }}
											displayEmpty
											className={classes.withoutPadding}
											labelId='demo-simple-select-outlined-label'
											id='demo-simple-select-outlined'
											value={quantity}
											onChange={handleChange}
											labelWidth={labelWidth}
											defaultValue={10}
										>
											<MenuItem value={1} selected>
												1
											</MenuItem>
											<MenuItem value={2}>2</MenuItem>
											<MenuItem value={3}>3</MenuItem>
											<MenuItem value={4}>4</MenuItem>
											<MenuItem value={5}>5</MenuItem>
											<MenuItem value={6}>6</MenuItem>
											<MenuItem value={7}>7</MenuItem>
											<MenuItem value={8}>8</MenuItem>
											<MenuItem value={9}>9</MenuItem>
											<MenuItem value={10}>10+</MenuItem>
										</Select>
									</FormControl>
								</Grid>
								<Grid item xs={3}>
									<Button style={{ color: '#0066C0', fontSize: 'small' }}>
										{' '}
										Delete
									</Button>
								</Grid>
							</Grid>
						</Grid>
						<Grid item xs={2} style={{ textAlign: 'right' }}>
							<Typography style={{}}>
								<span
									style={{
										fontSize: 'large',
										color: '#B12704 ',
										fontWeight: 'bolder'
									}}
								>
									$99.99
								</span>
							</Typography>
						</Grid>
					</Grid>
					<Divider></Divider>
				</Grid>
				<Grid item xs={3} style={{ marginTop: '28px' }}>
					<Paper
						elevation={0}
						style={{
							background: '#ddd',
							paddingTop: '20px',
							paddingBottom: '20px',
							paddingLeft: '20px',
							paddingRight: '20px'
						}}
					>
						<Grid container justify='space-between'>
							<Grid
								item
								xs={9}
								style={{ textAlign: 'left', paddingLeft: '0px' }}
							>
								<Typography>
									<span
										style={{
											paddingTop: '20px',
											fontSize: '1.2em'
										}}
									>
										Subtotal (2 items):
									</span>
								</Typography>
							</Grid>
							<Grid item xs={3}>
								<span
									style={{
										fontSize: 'large',
										color: '#B12704 ',
										fontWeight: 'bolder'
									}}
								>
									$186.99
								</span>
							</Grid>
						</Grid>

						<Grid container justify='space-between'>
							<Grid
								item
								xs={9}
								style={{ textAlign: 'left', paddingLeft: '0px' }}
							>
								<Typography variant='span'>
									<span
										style={{
											paddingTop: '20px',
											fontSize: '1.2em'
										}}
									>
										Frequent Buyer Points:
									</span>
								</Typography>
								<Checkbox checked={true}></Checkbox>
							</Grid>
							<Grid item xs={3}>
								<span
									style={{
										fontSize: 'large',
										color: '#B12704 ',
										fontWeight: 'bolder'
									}}
								>
									$10
								</span>
							</Grid>
						</Grid>

						<Grid container justify='space-between'>
							<Grid
								item
								xs={9}
								style={{ textAlign: 'left', paddingLeft: '0px' }}
							>
								<Typography>
									<span
										style={{
											paddingTop: '20px',
											fontSize: '1.2em'
										}}
									>
										Total:
									</span>
								</Typography>
							</Grid>
							<Grid item xs={3}>
								<span
									style={{
										fontSize: 'large',
										color: '#B12704 ',
										fontWeight: 'bolder'
									}}
								>
									$176.99
								</span>
							</Grid>
						</Grid>

						<Button
							style={{
								background: '#f0c14b',
								width: '100%',
								borderRadius: '0px',
								boxShadow: 'none',
								marginTop: '20px',
								marginRight: '20px'
							}}
							onClick={() => props.history.push('/review-order')}
						>
							Proceed to Buy
						</Button>
					</Paper>
				</Grid>
			</Grid>
		</div>
	);
}
