import React, { useState } from 'react';
import ItemsCarousel from 'react-items-carousel';
import range from 'lodash/range';
import styled from 'styled-components';

import Button from './button';
import NavigationIcon from '@material-ui/icons/Navigation';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import Card from './CardForCategories';
import Paper from '@material-ui/core/Paper';
import CardComponent from './CardComponent';
import book1 from '../book-1.jpg';
import book2 from '../book-2.jpg';
import book3 from '../book-3.jpg';
import book4 from '../book-4.jpg';
export default (props) => {
	const [activeItemIndex, setActiveItemIndex] = useState(0);
	const chevronWidth = 40;
	const similarItems = [
		{
			alt: 'book-alt-text',
			title: 'Look Both Ways',
			small: book1,
			medium: book1,
			large: book1,
			textSecondary: 'Book desscription',
			description: 'Detailed Book Description',
			price: 10
		},
		{
			alt: 'book-alt-text',
			title: 'Look Both Ways',
			small: book2,
			medium: book2,
			large: book2,
			textSecondary: 'Book desscription',
			description: 'Detailed Book Description',
			price: 10
		},
		{
			alt: 'book-alt-text',
			title: 'Look Both Ways',
			small: book3,
			medium: book3,
			large: book3,
			textSecondary: 'Book desscription',
			description: 'Detailed Book Description',
			price: 10
		},
		{
			alt: 'book-alt-text',
			title: 'Look Both Ways',
			small: book4,
			medium: book4,
			large: book4,
			textSecondary: 'Book desscription',
			description: 'Detailed Book Description',
			price: 10
		},
		{
			alt: 'book-alt-text',
			title: 'Look Both Ways',
			small: book1,
			medium: book1,
			large: book1,
			textSecondary: 'Book desscription',
			description: 'Detailed Book Description',
			price: 10
		}
	];
	return (
		<div style={{ padding: `0 ${chevronWidth}px 10px`, ...props }}>
			<ItemsCarousel
				requestToChangeActive={setActiveItemIndex}
				activeItemIndex={activeItemIndex}
				numberOfCards={4}
				gutter={20}
				leftChevron={
					<Button
						iconName={() => <ArrowBackIosIcon></ArrowBackIosIcon>}
					></Button>
				}
				rightChevron={
					<Button
						iconName={() => <ArrowForwardIosIcon></ArrowForwardIosIcon>}
					></Button>
				}
				style={{ background: 'white' }}
				outsideChevron
				chevronWidth={chevronWidth}
			>
				{similarItems.map((item, index) => (
					<CardComponent
					{...props}
						item={item}
						style={{ backgroundColor: 'lightgrey' }}
					></CardComponent>
				))}

				{/* <div style={{ height: 200, background: 'white' }}>First card</div> */}
				{/* <div style={{ height: 200, background: 'white' }}>Second card</div> */}
				{/* <div style={{ height: 200, background: '#EEE' }}>Third card</div> */}
				{/* <div style={{ height: 200, background: '#EEE' }}>Fourth card</div> */}
			</ItemsCarousel>
		</div>
	);
};
