import React from 'react';
import range from 'lodash/range';
import styled from 'styled-components';
import ItemsCarousel from 'react-items-carousel';
import Button from '../components/button';
import NavigationIcon from '@material-ui/icons/Navigation';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import Card from '../components/CardComponent';

const noOfItems = 3;
const noOfCards = 1;
const autoPlayDelay = 2000;
const chevronWidth = 40;

const Wrapper = styled.div`
	max-width: 100%;
	margin: 0 auto;
`;

const SlideItem = styled.div`
	height: 300px;
	background: #eee;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 20px;
	font-weight: bold;
`;

// const carouselItems = range(noOfItems).map((index) => (
// 	<SlideItem key={index}>{index + 1}</SlideItem>
// ));

export default class AutoPlayCarousel extends React.Component {
	state = {
		activeItemIndex: 0
	};

	componentDidMount() {
		this.interval = setInterval(this.tick, autoPlayDelay);
	}

	componentWillUnmount() {
		clearInterval(this.interval);
	}

	tick = () =>
		this.setState((prevState) => ({
			activeItemIndex:
				(prevState.activeItemIndex + 1) % (noOfItems - noOfCards + 1)
		}));

	onChange = (value) => this.setState({ activeItemIndex: value });

	render() {
		const carouselItems = this.props.content.map((item, index) => (
			<SlideItem key={index}>
				<img src={item.image} height='100%'></img>
			</SlideItem>
		));
		return (
			<Wrapper>
				<ItemsCarousel
					gutter={12}
					numberOfCards={noOfCards}
					activeItemIndex={this.state.activeItemIndex}
					requestToChangeActive={this.onChange}
					rightChevron={
						<Button
							style={{ paddingRight: '60px' }}
							iconName={() => <ArrowForwardIosIcon></ArrowForwardIosIcon>}
						></Button>
					}
					leftChevron={
						<Button
							style={{ paddingLeft: '60px' }}
							iconName={() => <ArrowBackIosIcon></ArrowBackIosIcon>}
						></Button>
					}
					chevronWidth={chevronWidth}
					outsideChevron
					children={carouselItems}
				/>
			</Wrapper>
		);
	}
}
