import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import ModalImage from 'react-modal-image';
import Fab from '@material-ui/core/Fab';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import Rating from '@material-ui/lab/Rating';
import './CardComponent.css';
// import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import BorderColorIcon from '@material-ui/icons/BorderColor';

const useStyles = makeStyles((theme) => ({
	card: {
		maxWidth: '100%',
		height: '100%',
		fontSize: '1rem'
		// overflow: 'hidden'
	},
	media: {
		height: 0,
		// paddingTop: '56.25%' // 16:9
		paddingTop: '50%'
	},
	expand: {
		transform: 'rotate(0deg)',
		marginLeft: 'auto',
		transition: theme.transitions.create('transform', {
			duration: theme.transitions.duration.shortest
		})
	},
	expandOpen: {
		transform: 'rotate(180deg)'
	},
	avatar: {
		backgroundColor: red[500]
	},
	cartContainer: {
		background: '#f0c14b'
		// alif
	}
}));

export default function RecipeReviewCard(props) {
	console.log(props);

	const classes = useStyles();
	const [expanded, setExpanded] = React.useState(false);

	const handleExpandClick = () => {
		setExpanded(!expanded);
	};

	return (
		<div style={{ width: '100%', marginLeft: '10px' }} className={'ribbon'}>
			{props.item.signedBook ? <span className='ribbon3'>Signed</span> : null}
			<Card
				className={classes.card}
				style={{ ...props.style }}
				elevation={props.elevation || 3}
			>
				<Typography align='center' noWrap>
					<div style={{ marginTop: '10px', marginBottom: '0px' }}>
						{props.item.title}
					</div>
					{props.item.subheader}
				</Typography>
				{/* <CardMedia
				className={classes.media}
				image='http://images5.fanpop.com/image/photos/31000000/New-Shatter-Me-Book-cover-shatter-me-31085216-1063-1600.jpg'
				title='Paella dish'
			/> */}

				<CardContent style={{ paddingTop: '5px', paddingBottom: '1px' }}>
					<div>
						{props.zoomable ? (
							<ModalImage
								alt={props.item.alt}
								small={props.item.small}
								medium={props.item.medium}
								large={props.item.large}
							/>
						) : (
							<CardMedia
								className={classes.media}
								image={props.item.medium}
								title={props.item.textSecondary}
								alt={props.item.alt}
								style={{ cursor: 'pointer' }}
								onClick={() => {
									console.log('[CardComponent] Props are', props);
									props.history.push(
										`/product-details/${props.item.title
											.toLowerCase()
											.split(' ')
											.join('-')}`
									);
								}}
							/>
						)}
					</div>
					{props.item.title === 'Look Both Ways' ? (
						<div style={{ textAlign: 'center' }}>
							<Typography
								variant='body2'
								color='textSecondary'
								component='div'
								style={{ justifyContent: 'center' }}
							>
								<Rating name='read-only' value={3} readOnly />
							</Typography>
							{/* <Chip
								size='small'
								avatar={<BorderColorIcon></BorderColorIcon>}
								label='Signed Book'
								clickable
								color='secondary'
							/> */}
						</div>
					) : (
						<div>
							<Typography
								variant='body2'
								color='textSecondary'
								component='div'
								style={{ justifyContent: 'center' }}
							>
								{/* {props.item.textSecondary} */}
								<Rating
									name='read-only'
									value={3}
									style={{ visibility: 'hidden' }}
								/>
							</Typography>
							{/* <Chip
								size='small'
								avatar={<BorderColorIcon></BorderColorIcon>}
								label='Signed Book'
								clickable
								color='secondary'
								style={{ visibility: 'hidden' }}
							/> */}
						</div>
					)}
				</CardContent>
				<CardActions
					disableSpacing
					style={{ 'justify-content': 'space-evenly', paddingBottom: '10px' }}
				>
					<IconButton aria-label='add to favorites'>
						<FavoriteIcon />
					</IconButton>
					<IconButton aria-label='share'>
						<ShareIcon />
					</IconButton>

					{!props.item.isCategory ? (
						<Fab variant='extended' className={classes.cartContainer}>
							<AddShoppingCartIcon className={classes.extendedIcon} /> $
							{props.item.price}
						</Fab>
					) : null}

					{/* <IconButton
					className={clsx(classes.expand, {
						[classes.expandOpen]: expanded
					})}
					onClick={handleExpandClick}
					aria-expanded={expanded}
					aria-label='show more'
					>
					<ExpandMoreIcon />
				</IconButton> */}
				</CardActions>
			</Card>
		</div>
	);
}
