import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { Typography, Card } from '@material-ui/core';
import plus from './plus';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'left',
		color: theme.palette.text.secondary
	}
}));

export default function Address() {
	const classes = useStyles();
	const [anchorEl, setAnchorEl] = React.useState(null);

	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};

	return (
		<div className={classes.root}>
			<Grid container direction='row' justify='flex-start' alignItems='center'>
				<Grid item xs={12}>
					<Typography
						style={{
							color: 'black',
							textAlign: 'Left',
							fontSize: '18px',
							fontWeight: 500
						}}
					>
						Manage Addresses
					</Typography>
				</Grid>
				<Grid
					item
					xs={12}
					style={{
						marginTop: '10px',
						marginBottom: '30px',
						cursor: 'pointer'
					}}
				>
					<Card
						variant='outlined'
						style={{ background: '#eee', padding: '20px' }}
					>
						<Typography
							style={{
								color: 'black',
								textAlign: 'Left',
								fontSize: '14px',
								fontWeight: 500
							}}
						>
							<img src={plus}></img>{' '}
							<span
								style={{
									color: '#2874f0',
									textAlign: 'Left',
									fontSize: '14px',
									fontWeight: 500
								}}
							>
								ADD A NEW ADDRESS
							</span>
						</Typography>
					</Card>
				</Grid>
				{Array(9)
					.fill(1)
					.map(() => {
						return (
							<Grid item xs={12} style={{ marginTop: '10px' }}>
								<Card
									variant='outlined'
									style={{ background: '#eee', padding: '20px' }}
								>
									<Grid
										container
										direction='row'
										justify='space-between'
										alignItems='center'
									>
										<Grid item xs={9}>
											<Typography
												style={{
													color: 'black',
													textAlign: 'Left',
													fontSize: '14px',
													fontWeight: 500,
													lineHeight: 1.5
												}}
											>
												<span
													style={{
														textAlign: 'Left',
														fontSize: '14px',
														fontWeight: 500
													}}
												>
													John Doe
												</span>
												<span
													style={{
														textAlign: 'Left',
														fontSize: '14px',
														fontWeight: 500,
														paddingLeft: '10px'
													}}
												>
													+112345678
												</span>
											</Typography>
											<Typography
												style={{
													textAlign: 'Left',
													fontSize: '14px'
													// fontWeight: 500
													// paddingLeft: '10px'
												}}
											>
												101 Sanford Farm Shpg Center, Amsterdam NY
											</Typography>
											<Typography
												style={{
													textAlign: 'Left',
													fontSize: '14px',
													fontWeight: 500
												}}
											>
												12010
											</Typography>
										</Grid>
										<Grid item xs={2} style={{ textAlign: 'right' }}>
											<MoreVertIcon
												style={{ cursor: 'pointer' }}
												onClick={handleClick}
											></MoreVertIcon>
											<Menu
												disableScrollLock={true}
												id='simple-menu'
												anchorEl={anchorEl}
												keepMounted
												open={Boolean(anchorEl)}
												onClose={handleClose}
											>
												<MenuItem onClick={handleClose}>Edit</MenuItem>
												<MenuItem onClick={handleClose}>Delete</MenuItem>
											</Menu>
										</Grid>
									</Grid>
								</Card>
							</Grid>
						);
					})}
			</Grid>
		</div>
	);
}
