import React from 'react';
import Zoom from 'react-reveal/Zoom';
import first from '../1.png';
import second from '../2.png';
import third from '../3.png';
import fourth from '../4.png';
import CarouselForLandingPage from './CarouselForLandingPage';
import Paper from './Paper.js';
import BannerCarousel from './BannerCarousel';
import banner1 from '../banner-1.jpg';
import banner2 from '../banner-2.jpg';
import banner3 from '../banner-3.jpg';
import CarouselForCategories from './CarouselForCategories';
import EmbeddedPostComponent from './EmbeddedPostComponent';
import MarqueeText from '../MarqueeText';
import FacebookPostsCarousels from './FacebookPostsCarousels';
export default function LandingPage(props) {
	const banners = [{ image: banner1 }, { image: banner2 }, { image: banner3 }];
	return (
		<div>
			{/* <MarqueeText></MarqueeText> */}
			<BannerCarousel content={banners}></BannerCarousel>
			<Paper content={'Featured Books'} style={{ marginBottom: 0 }}></Paper>
			{/* <Zoom> */}
			<CarouselForLandingPage
				{...props}
				content={props.featured}
			></CarouselForLandingPage>
			{/* <img
						src={first}
						style={{
							maxWidth: '100%',
							height: 'auto'
						}}
					></img> */}
			{/* </Zoom> */}
			<Zoom>
				<Paper content={'Books Categories'}></Paper>
				<CarouselForCategories
					{...props}
					content={props.category}
				></CarouselForCategories>
			</Zoom>
			<Zoom>
				{/* <img
					src={second}
					style={{
						maxWidth: '100%',
						height: 'auto'
					}}
				></img> */}
				{/* <img
					src={third}
					style={{
						maxWidth: '100%',
						height: 'auto'
					}}
					></img>
					<img
					src={fourth}
					style={{
						maxWidth: '100%',
						height: 'auto'
					}}
				></img> */}
				<Paper content={'Recommended Books'}></Paper>
				<CarouselForLandingPage
					{...props}
					content={props.recommended}
				></CarouselForLandingPage>
			</Zoom>
			<FacebookPostsCarousels></FacebookPostsCarousels>
		</div>
	);
}
