import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import CardComponent from './CardComponent';
import Checkbox from '@material-ui/core/Checkbox';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Rating from '@material-ui/lab/Rating';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Input from '@material-ui/core/Input';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import './HomeContent.css';
const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
		background: 'white',
		paddingRight: '1.5vw'
	},
	paper: {
		padding: theme.spacing(1),
		textAlign: 'center',
		color: theme.palette.text.secondary,
		boxShadow: 'none'
	},
	formControl: {
		margin: theme.spacing(3)
	},
	heading: {
		fontSize: theme.typography.pxToRem(15),
		fontWeight: theme.typography.fontWeightRegular
	}
}));

const price = [
	{
		value: 100,
		label: '$100'
	},
	{
		value: 200,
		label: '$200'
	},
	{
		value: 300,
		label: '$300'
	},
	{
		value: 400,
		label: '$400'
	}
];

function valuetext(value) {
	return `$${value}`;
}

function valueLabelFormat(value) {
	return price.findIndex((mark) => mark.value === value) + 1;
}

export default function CenteredGrid(props) {
	const classes = useStyles();
	const [checked, setChecked] = React.useState(true);

	// const handleChange = (event) => {
	// 	setChecked(event.target.checked);
	// };
	function valuetext(value) {
		return `${value}$`;
	}
	const [value, setValue] = React.useState([20, 37]);

	const handleChange = (event, newValue) => {
		setValue(newValue);
	};

	return (
		<div className={classes.root}>
			<Grid container spacing={3}>
				<Grid item xs={3}>
					<Paper className={classes.paper} style={{ paddingRight: '0px' }}>
						<div className={classes.root} style={{ paddingRight: '0px' }}>
							<Typography
								id='discrete-slider'
								gutterBottom
								style={{ color: 'black' }}
							>
								Price: $
							</Typography>
							<Slider
								value={value}
								onChange={handleChange}
								valueLabelDisplay='auto'
								aria-labelledby='range-slider'
								getAriaValueText={valuetext}
							/>
							<Grid
								container
								direction='row'
								justify='space-between'
								alignItems='center'
							>
								<Grid item xs={6} style={{ textAlign: 'left' }}>
									Min:
									<input
										type='number'
										style={{ width: '50px', marginLeft: '10px' }}
										value={value[0]}
										onChange={(event) =>
											handleChange(event, [event.target.value, value[1]])
										}
									></input>
								</Grid>
								<Grid item xs={6} style={{ textAlign: 'right' }}>
									Max:
									<input
										type='number'
										style={{ width: '50px', marginLeft: '10px' }}
										value={value[1]}
										onChange={(event) =>
											handleChange(event, [value[0], event.target.value])
										}
									></input>
								</Grid>
							</Grid>
							<div style={{ paddingBottom: '15px', 'text-align': 'left' }}>
								{/* <div style={{ display: 'inline', justifyContent: 'left' }}> */}
								&nbsp;&nbsp;&nbsp;
								{/* </div> */}
							</div>
							<div className={classes.margin} />

							<ExpansionPanel>
								<ExpansionPanelSummary
									expandIcon={<ExpandMoreIcon />}
									aria-controls='panel1a-content'
									id='panel1a-header'
								>
									<Typography className={classes.heading}>
										Customer Ratings
									</Typography>
								</ExpansionPanelSummary>
								<ExpansionPanelDetails>
									<FormGroup>
										<FormControlLabel
											control={
												<Checkbox
													// checked={gilad}
													// onChange={handleChange('gilad')}
													value='gilad'
												/>
											}
											label={<Rating name='read-only' value={5} readOnly />}
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={gilad}
													// onChange={handleChange('gilad')}
													value='gilad'
												/>
											}
											label={<Rating name='read-only' value={4} readOnly />}
										/>
										<FormControlLabel
											control={
												<Checkbox
													defaultChecked
													// onChange={handleChange('gilad')}
													value='gilad'
												/>
											}
											label={<Rating name='read-only' value={3} readOnly />}
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label={<Rating name='read-only' value={2} readOnly />}
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={antoine}
													// onChange={handleChange('antoine')}
													value='antoine'
												/>
											}
											label={<Rating name='read-only' value={1} readOnly />}
										/>
									</FormGroup>
								</ExpansionPanelDetails>
							</ExpansionPanel>

							<div className={classes.margin} />

							<ExpansionPanel>
								<ExpansionPanelSummary
									expandIcon={<ExpandMoreIcon />}
									aria-controls='panel1a-content'
									id='panel1a-header'
								>
									<Typography className={classes.heading}>Author</Typography>
								</ExpansionPanelSummary>
								<ExpansionPanelDetails>
									<FormGroup>
										<FormControlLabel
											control={
												<Checkbox
													// checked={gilad}
													// onChange={handleChange('gilad')}
													value='gilad'
												/>
											}
											label='Ali Wong'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={gilad}
													// onChange={handleChange('gilad')}
													value='gilad'
												/>
											}
											label='Kiersten White'
										/>
										<FormControlLabel
											control={
												<Checkbox
													defaultChecked
													// onChange={handleChange('gilad')}
													value='gilad'
												/>
											}
											label='Gabrielle Bernstein'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Jason Reynolds'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={antoine}
													// onChange={handleChange('antoine')}
													value='antoine'
												/>
											}
											label=' Grace Byers'
										/>
									</FormGroup>
								</ExpansionPanelDetails>
							</ExpansionPanel>

							<div className={classes.margin} />

							<ExpansionPanel>
								<ExpansionPanelSummary
									expandIcon={<ExpandMoreIcon />}
									aria-controls='panel1a-content'
									id='panel1a-header'
								>
									<Typography className={classes.heading}>Binding</Typography>
								</ExpansionPanelSummary>
								<ExpansionPanelDetails>
									<FormGroup>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Hardcover'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={antoine}
													// onChange={handleChange('antoine')}
													value='antoine'
												/>
											}
											label='Paperback'
										/>
									</FormGroup>
								</ExpansionPanelDetails>
							</ExpansionPanel>

							<div className={classes.margin} />

							<ExpansionPanel>
								<ExpansionPanelSummary
									expandIcon={<ExpandMoreIcon />}
									aria-controls='panel1a-content'
									id='panel1a-header'
								>
									<Typography className={classes.heading}>
										Categories
									</Typography>
								</ExpansionPanelSummary>
								<ExpansionPanelDetails>
									<FormGroup>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Fiction'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Literature'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Drama'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='History'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Religion'
										/>

										<FormControlLabel
											control={
												<Checkbox
													// checked={antoine}
													// onChange={handleChange('antoine')}
													value='antoine'
												/>
											}
											label='Art'
										/>
									</FormGroup>
								</ExpansionPanelDetails>
							</ExpansionPanel>

							<div className={classes.margin} />

							<ExpansionPanel>
								<ExpansionPanelSummary
									expandIcon={<ExpandMoreIcon />}
									aria-controls='panel1a-content'
									id='panel1a-header'
								>
									<Typography className={classes.heading}>Publisher</Typography>
								</ExpansionPanelSummary>
								<ExpansionPanelDetails>
									<FormGroup>
										<FormControlLabel
											control={
												<Checkbox
													// checked={jason}
													// onChange={handleChange('jason')}
													value='jason'
												/>
											}
											label='Penguin'
										/>
										<FormControlLabel
											control={
												<Checkbox
													// checked={antoine}
													// onChange={handleChange('antoine')}
													value='antoine'
												/>
											}
											label='Bloomhouse'
										/>
									</FormGroup>
								</ExpansionPanelDetails>
							</ExpansionPanel>
						</div>
					</Paper>
				</Grid>
				<Grid container xs={9}>
					{props.items.map((ele, index) => {
						return (
							<Grid item xs={3}>
								<Paper className={classes.paper} elevation={0}>
									<CardComponent
										{...props}
										item={ele}
										style={{ background: '#eee' }}
										elevation={0}
									></CardComponent>
								</Paper>
							</Grid>
						);
					})}
					<Grid container xs={12}>
						<Grid item xs={12}>
							<Paper
								style={{
									textAlign: 'center',
									background: 'white',
									marginBottom: '20px',
									boxShadow: 'none',
									paddingTop: '5px',
									marginTop: '5px'
								}}
								elevation={10}
							>
								<span
									className='hover-item'
									style={{
										textAlign: 'center',
										height: '100px',
										cursor: 'pointer',
										fontWeight: 'bolder',
										fontSize: '3vh',
										color: ''
									}}
								>
									Load More
								</span>
							</Paper>
						</Grid>
					</Grid>
				</Grid>
			</Grid>
		</div>
	);
}
